import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, ObjectKeys, genNumId, qryStore, fetchEncryptStream, parse4Dig, genAlphDay, generateComprobanteId } from "../../../stores/Utils";

import { TextInput_Cmpt,  AccountLabelDetail_Cmpt } from "../../../components";


import "./styles.css"
import { PickEmployees_Cmpt, PickProvider_Cmpt, PickCustomer_Cmpt } from "..";
import { CloseModal } from "../../../components/DialogHRM";
import { DateCalComponent_Cmpt } from "../../Inventory";
import { getLastSequence } from "../services/server_fetching";
import { useNavigate } from "react-router";

const TextInput = TextInput_Cmpt();





const AccountLabelDetail = AccountLabelDetail_Cmpt();

const PickEmployees = PickEmployees_Cmpt();
const PickProvider = PickProvider_Cmpt();

const PickCustomer = PickCustomer_Cmpt();

const DateCalComponent =DateCalComponent_Cmpt();


const frmNm = "entry_operation_comprobante"

const RegisterComprobante  = ({ data, confirm, item }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const navigate = useNavigate();


	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updStore("accountPicker", {});
			updFrm("description",item.name)
			updFrm("customerId",null)
			updFrm("providerId",null)
			let accountsToParameterize = item.accountsToParameterize;
			updStore("multi_params",accountsToParameterize);
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};



	const lastId  = async ()  => {
		getLastSequence(save)

	}



	const save  = async ()  => {
		let ffm = {...getStore(frmNm)};

		if(!ffm["date"]){
			ffm["date"] = (new Date()).getTime()+800;
		}
		else{
			ffm["date"] =(new Date(ffm["date"])).getTime();
		}

		let comprobanteId = await generateComprobanteId(ffm["date"]);

		ffm["comprobanteId"] =comprobanteId;

		
		ffm["document"] = ffm["document"]?.trim();

		let comp2save = [];




		

		getStore("multi_params") && ObjectKeys(getStore("multi_params"))?.map(async (element, indE)=>{
			let dataAcc = getStore("multi_params")[element];
			if(!isNaN(dataAcc["amount"] * 1) && dataAcc["amount"]*1 > 0){
				let nnfrm = {...ffm};

				if(item?.extra === "recurrent"){
					nnfrm["document"] =  dataAcc["document"];
				}
				nnfrm["account"] =  dataAcc["account"];
				nnfrm["subAccount"] =  dataAcc["subAccount"];
				nnfrm["type"] =  dataAcc["type"];
				nnfrm["amount"] =  dataAcc["amount"] *1;
				nnfrm["operationId"] = gen16CodeId();
				delete nnfrm["providerId"];
				let accountDeb = getStore("account_ids")?.[dataAcc["account"]];
				if(accountDeb.payOrCollect){
					nnfrm["payOrCollect"] = accountDeb.payOrCollect;
				}

				let bdyq5 = {...getStore(qryStore())?.add_opera};
				bdyq5.params = {
					businessId:  getStore("profileUser")?.businessId
				}
				bdyq5.form=nnfrm;
				comp2save.push(bdyq5)
			}
		});


		if(item?.extra === "provider" && getStore(frmNm)?.["providerId"] ){
			let nnfrm2 = {...ffm};
			nnfrm2["type"] =  "credit";
			let provData = getStore("providers_ids")[getStore(frmNm)["providerId"]];
			nnfrm2["account"] =  provData["account"];
			nnfrm2["subAccount"] =  provData["subAccount"];
			nnfrm2["amount"] =  sum(getStore("multi_params"));
			nnfrm2["operationId"] = gen16CodeId();
			let accountDeb = getStore("account_ids")?.[provData["account"]];
			if(accountDeb.payOrCollect){
				nnfrm2["payOrCollect"] = accountDeb.payOrCollect;
			}
			
			let bdyq3 = {...getStore(qryStore())?.add_opera};
			bdyq3.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq3.form=nnfrm2;
			comp2save.push(bdyq3)
		}

		if(item?.extra === "customer" && getStore(frmNm)?.["customerId"] ){
			let nnfrm3 = {...ffm};
			nnfrm3["type"] =  "debit";
			let provData = getStore("providers_ids")[getStore(frmNm)["customerId"]];
			nnfrm3["account"] =  provData["account"];
			nnfrm3["subAccount"] =  provData["subAccount"];
			nnfrm3["amount"] =  sum(getStore("multi_params")) * -1;
			nnfrm3["operationId"] = gen16CodeId();
			let accountDeb = getStore("account_ids")?.[provData["account"]];
			if(accountDeb.payOrCollect){
				nnfrm3["payOrCollect"] = accountDeb.payOrCollect;
			}
			
			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm3;
			comp2save.push(bdyq2)
		}


		/** */
		const promises = comp2save.map(async (qry2Sv) => {
			const res = fetchEncryptStream(qry2Sv);
			const td = await res;
			if (td) {
				//console.log(td)
			}
        })      
        await Promise.all(promises);

		// 

		
		
		setTimeout(() => {
			updStore("multi_params", null)
			updStore(frmNm, null)
			data?.modalID && CloseModal({id:data?.modalID});
			navigate({
				pathname: "/accounting-ledger" ,
				search: `?cId=${comprobanteId}`
			});
		}, 400);
		
		
	};


	let isValidPC = ((item?.extra === "provider" && getStore(frmNm)?.["providerId"])  || (item?.extra === "customer" && getStore(frmNm)?.["customerId"]))  && sum(getStore("multi_params"))!==0



	let hasDoc = item?.extra==="recurrent" || getStore(frmNm)?.["document"];
	return (
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "960px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Asiento Manual
				</p>
				<div className="flexSpace"/>
				
			</div>

			<div className="_dsplFlx ">
				<DateCalComponent init={getStore(frmNm)?.["date"]} updDate={(e)=>updFrm("date",e)} />
               
				<div className="flexSpace"/>
				
				{item?.extra==="recurrent"?null:
				<TextInput  
					init={getStore(frmNm)?.["document"]} 
					label={"Documento"} 
					width={120} 
					updChanges={(e)=>updFrm("document",e)}
				/>
	}
			</div>
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>
			



			{item?.extra === "employee"?
				<div className={"extra_container"}>
					{getStore(frmNm)?.["employeeId"]?
						<div className="_dsplFlx"  >
							<div className="employee_title" style={{color: "#646cff", cursor: "pointer"}} onClick={()=>updFrm("employeeId",null)}> 
								Empleado:
							</div>
							<div className="employee_name">
								{getStore("employees_ids")[getStore(frmNm)["employeeId"]]?.name}
							</div>
						</div>
					:
					<PickEmployees 
						//placeholder={"buscar una cuenta"} 
						width={420}
						handleClick={(v)=>updFrm("employeeId",v)}
					/>
					}
				</div>
			:null}


			{item?.extra === "provider"?
				<div className={"extra_container"}>
					{getStore(frmNm)?.["providerId"]?
						<div className="_dsplFlx"  >
							<div className="employee_title" style={{color: "#646cff", cursor: "pointer"}} onClick={()=>updFrm("providerId",null)}> 
								Provedor:
							</div>
							<div className="employee_name">
								{getStore("providers_ids")[getStore(frmNm)["providerId"]]?.name}
							</div>
						</div>
					:
					<PickProvider 
						//placeholder={"buscar una cuenta"} 
						width={420}
						handleClick={(v)=>updFrm("providerId",v?.providerId)}
					/>
					}
				</div>
			:null}


			{item?.extra === "customer"?
				<div className={"extra_container"}>
					{getStore(frmNm)?.["customerId"]?
						<div className="_dsplFlx"  >
							<div className="employee_title" style={{color: "#646cff", cursor: "pointer"}} onClick={()=>updFrm("customerId",null)}> 
								Cliente:
							</div>
							<div className="employee_name">
								{getStore("providers_ids")[getStore(frmNm)["customerId"]]?.name}
							</div>
						</div>
					:
					<PickCustomer 
						//placeholder={"buscar una cuenta"} 
						width={420}
						handleClick={(v)=>updFrm("customerId",v?.providerId)}
					/>
					}
				</div>
			:null}

			
			<div className="boxAddAcc" style={{margin: "18px 0"}}>

				<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
					<div className="tr_bar_25">
						Cuenta
					</div>
					<div className="tr_bar_25">
						Sub Cuenta
					</div>
					<div className="tr_bar_25">
						{item?.extra==="recurrent"?"Documento":""}
					</div>
					<div className="tr_bar_25">
						Importe
					</div>
				</div>
				{getStore(frmNm)?.["customerId"]?
					<ProviderAccount 
						idKey={getStore(frmNm)["customerId"]}
						data={getStore("providers_ids")[getStore(frmNm)["customerId"]]}
						total={sum(getStore("multi_params"))}
						type={"debit"}
					/>
				:null}
				{getStore("multi_params") && ObjectKeys(getStore("multi_params"))?.map((element, indE)=>
				
					<ListingAccounts 
						key={element} 
						idKey={element} 
						data={getStore("multi_params")[element]}
						updObs={set_obs}
						isRecurrent={item?.extra==="recurrent"}
					/>
				)}
				{getStore(frmNm)?.["providerId"]?
					<ProviderAccount 
						idKey={getStore(frmNm)["providerId"]}
						data={getStore("providers_ids")[getStore(frmNm)["providerId"]]}
						total={sum(getStore("multi_params"))}
						type={"credit"}
					/>
				:null}

				


			</div>
			

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{hasDoc && (isValidPC ||   EstaCuadrado(getStore("multi_params")))?
				<div className="card">
					<button className="group_title addBtn purple" onClick={save}>
						Guardar
					</button>
				</div>
				:null}
			</div>
		

			
		</div>
	);
};

export default RegisterComprobante;


const ListingAccounts  = ({ data, updObs, idKey, isRecurrent }) => {
	const [amount, set_amount] = useState(0);



	const handleChange = (v) => {
		//set_amount(v);
		updFrm("amount",v, idKey)
		updObs(gen16CodeId())
	}
	
	const handleDocChange = (v) => {
		//set_amount(v);
		updFrm("document",v, idKey)
		updObs(gen16CodeId())
	}
	
	
	
	const updFrm = (f, v, k) => {
		let frm2 = getStore("multi_params") || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore("multi_params", frm2);
	};

	let accountD = getStore("account_ids")?.[data?.account];
	let subaccountD = getStore("sub_account_ids")?.[data?.subAccount];
	

	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar_25" style={{marginTop: 10 , fontWeight: 600, color: data.type === "debit"? "#009688":"#c62828"}}>
				<AccountLabelDetail 
					data={accountD} 
					color={ data.type === "debit"? "#009688":"#c62828"}
					labelField={"code"}
					infoField={"name"}
				/>
			</div>
			<div className="tr_bar_25" style={{marginTop: 10}}>
				<AccountLabelDetail 
					data={subaccountD} 
					color={ data.type === "debit"? "#009688":"#c62828"}
					labelField={"subAccount"}
					infoField={"subAccountName"}
				/>
			</div>
			<div className="tr_bar_25" style={{marginTop: 10}}>

			{isRecurrent?
				<TextInput
					width={120}
					init={data?.["document"]} 
					updChanges={handleDocChange}
				/>
			:null}
				
			</div>
			<div className="tr_bar_25">
				<TextInput  
					init={amount} 
					//label={"Descripcion"} 
					type={"number"}
					width={80}
					init={data?.["amount"]} 
					updChanges={handleChange}
				/>
			</div>
		</div>
	)

}



const ProviderAccount  = ({ data, idKey, total, type }) => {
	const [amount, set_amount] = useState(0);

	
	
	const updFrm = (f, v, k) => {
		let frm2 = getStore("multi_params") || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore("multi_params", frm2);
	};

	let accountD = getStore("account_ids")?.[data?.account];
	let subaccountD = getStore("sub_account_ids")?.[data?.subAccount];
	

	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar_25" style={{marginTop: 10 , fontWeight: 600, color: type === "debit"? "#009688":"#c62828"}}>
				<AccountLabelDetail 
					data={accountD} 
					color={ type === "debit"? "#009688":"#c62828"}
					labelField={"code"}
					infoField={"name"}
				/>
			</div>
			<div className="tr_bar_25" style={{marginTop: 10}}>
				<AccountLabelDetail 
					data={subaccountD} 
					color={ type === "debit"? "#009688":"#c62828"}
					labelField={"subAccount"}
					infoField={"subAccountName"}
				/>
			</div>
			<div className="tr_bar_25" style={{marginTop: 10}}>
				
			</div>
			<div className="tr_bar_25" style={{marginTop: 10}}>
				<div className={"total_cntra"}>
					{total * (type==="debit"?-1:1).toFixed(2)}
				</div>
			</div>
		</div>
	)

}





const sum = (obj) => {
	let balance = 0;
	let arr = obj && ObjectKeys(obj);
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let newB = balance.toFixed(2);
		let amount = data?.amount ? data?.type === "debit" ?  parseFloat(data.amount) :parseFloat(data.amount) *-1  : 0;
		balance = amount + parseFloat(newB);
		
	});
	return balance;
}  

const EstaCuadrado = (obj) => {
	let balance = 0;
	let arr = obj && ObjectKeys(obj);
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let newB = balance.toFixed(2);
		let amount = data?.amount ? data?.type === "debit" ?  parseFloat(data.amount) :parseFloat(data.amount) *-1  : 0;
		balance = amount + parseFloat(newB);
		
	});
	return arr && arr?.length>0 && balance === 0;
}  